import { Badge } from "@acme/ui/badge";
import { Button } from "@acme/ui/button";
import { Progress } from "@acme/ui/progress";
import { useSidebar } from "@acme/ui/sidebar";

import { User } from "../types";

interface UsageItemProps {
  label: string;
  value: number;
  max: number;
}

export const UsageItem = ({ label, value, max }: UsageItemProps) => {
  return (
    <div className="flex flex-col items-start justify-between gap-0.5">
      <p className="flex w-full items-center gap-2">
        <span className="text-sm text-foreground">{label}</span>
        <span className="ml-auto text-xs text-muted-foreground">
          {value}/{max}
        </span>
      </p>
      <Progress value={(value / max) * 100} max={100} />
    </div>
  );
};

export default function UsageInfo({ user }: { user: User }) {
  const { state } = useSidebar();
  const isCollapsed = state === "collapsed";

  if (isCollapsed) {
    return null;
  }

  return (
    <div className="flex flex-col gap-2 rounded-md border p-4">
      <div className="flex flex-col gap-2">
        <div className="flex gap-2">
          <p className="text-sm font-semibold text-foreground">Usage</p>
          <Badge variant="outline">Free</Badge>
        </div>
        <UsageItem
          label="Storage used"
          value={user.limits.storage_uploaded}
          max={user.limits.storage_limit}
        />
        <UsageItem
          label="Uploads used"
          value={user.limits.uploads}
          max={user.limits.uploads_limit}
        />
      </div>

      <p className="text-xs text-muted-foreground">
        Usage will reset Apr 8, 2025
      </p>

      <Button variant="primary" className="w-full">
        Get Pro Version
      </Button>
    </div>
  );
}
